@import url('https://fonts.googleapis.com/css?family=Lobster');

ul {
  padding-left: 0;
}

h1 {
  -ms-flex: 3 1;
      flex: 3 1;
  font-family: 'Lobster', cursive;
  font-size: 60px;
  margin: 0;
}

img {
  height: 250px;
}

button {
  background: url('./rocket.png') no-repeat center;
  background-size: 40px;
  width: 90px;
  height: 90px;
  border: 5px solid #000;
  border-radius: 50px;
  background-color: #000;
  color: #00fff9;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.main {
  width: 40%;
}

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  padding-top: 20px;
  padding-bottom: 20px;
}

.world-wrapper {
  display: -ms-flexbox;
  display: flex;
}

.world-info {
  padding-left: 50px;
  width: 100%;
}

.world-name {
  font-size: 30px;
  border-bottom: solid 1px;
  margin: 0px;
}

.world-attributes {
  margin: 10px 0 0 0;
}

@-webkit-keyframes glowing {
  0% { -webkit-box-shadow: 0 0 -20px #00c4bf; box-shadow: 0 0 -20px #00c4bf; }
  40% { -webkit-box-shadow: 0 0 50px #00c4bf; box-shadow: 0 0 50px #00c4bf; }
  60% { -webkit-box-shadow: 0 0 50px #00c4bf; box-shadow: 0 0 50px #00c4bf; }
  100% { -webkit-box-shadow: 0 0 -20px #00c4bf; box-shadow: 0 0 -20px #00c4bf; }
}

@keyframes glowing {
  0% { -webkit-box-shadow: 0 0 -20px #00c4bf; box-shadow: 0 0 -20px #00c4bf; }
  40% { -webkit-box-shadow: 0 0 50px #00c4bf; box-shadow: 0 0 50px #00c4bf; }
  60% { -webkit-box-shadow: 0 0 50px #00c4bf; box-shadow: 0 0 50px #00c4bf; }
  100% { -webkit-box-shadow: 0 0 -20px #00c4bf; box-shadow: 0 0 -20px #00c4bf; }
}

.button-glow {
  -webkit-animation: glowing 3000ms infinite;
          animation: glowing 3000ms infinite;
}



